.header {
  background: #000 url(../img/main-screen__bgc.jpg) no-repeat center center/cover;
  padding-top: 40px;
}
@media (max-width: 960px) {
  .header {
    padding-top: 30px;
  }
}
@media (max-width: 760px) {
  .header {
    padding-top: 20px;
  }
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1792px;
  margin: 0 auto;
  margin-bottom: 190px;
  padding-right: 16px;
  padding-left: 16px;
}
@media (max-width: 960px) {
  .header__wrap {
    margin-bottom: 110px;
  }
}
@media (max-width: 760px) {
  .header__wrap {
    margin-bottom: 90px;
  }
}
@media (max-width: 425px) {
  .header__wrap {
    margin-bottom: 40px;
  }
}
.header__logo {
  margin-right: 120px;
}
@media (max-width: 1010px) {
  .header__logo {
    margin-right: 0;
  }
}

.nav {
  padding: 0 10px;
  transition: opacity 0.2s ease, transfrom 0.2s ease;
}
.nav__list {
  list-style-type: none;
  display: flex;
}
@media (max-width: 760px) {
  .nav__list {
    margin: auto;
  }
}
.nav__item {
  margin-right: 56px;
}
.nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 960px) {
  .nav__item {
    margin-right: 30px;
  }
}
.nav__link {
  display: block;
  font-family: var(--font-family);
  text-decoration: none;
  color: var(--light-color);
  font-size: 16px;
  line-height: 1.2;
  transition: color 0.2s ease-in;
}
.nav__link:hover {
  color: #df5950;
}
@media (max-width: 960px) {
  .nav__link {
    font-size: 15px;
  }
}
@media (max-width: 760px) {
  .nav__link {
    font-size: 24px;
    padding: 10px 0;
  }
}
@media (max-width: 760px) {
  .nav {
    position: fixed;
    opacity: 0;
    transform: translateX(-100%);
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
  }
  .nav._active {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: fixed;
    opacity: 1;
    transform: translateX(0);
    background-color: rgba(12, 12, 12, 0.95);
    padding: 40px 16px;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 54;
    overflow: auto;
  }
  .nav .nav__list {
    flex-direction: column;
  }
  .nav .nav__item {
    margin-right: 0;
    margin-bottom: 10px;
  }
  .nav .nav__item:last-child {
    margin-bottom: 0;
  }
  .nav .nav__link {
    font-weight: 500;
  }
  .nav .nav__link:hover {
    color: var(--light-color);
  }
}

.count {
  display: flex;
}
.count._modify {
  margin-top: 50px;
}
@media (max-width: 760px) {
  .count._modify {
    margin-top: 40px;
  }
}
.count__item {
  background-color: var(--second-bg);
  padding: 20px 30px;
  margin-right: 30px;
  min-width: 105px;
}
.count__item:last-child {
  margin-right: 0;
}
@media (max-width: 960px) {
  .count__item {
    padding: 15px 25px;
    min-width: 100px;
  }
}
@media (max-width: 760px) {
  .count__item {
    padding: 10px 20px;
    margin-right: 20px;
    min-width: 80px;
  }
}
@media (max-width: 425px) {
  .count__item {
    padding: 10px 12px;
    margin-right: 15px;
    min-width: 60px;
  }
}
.count__number {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 36px;
  line-height: 1.3;
  color: var(--light-color);
  text-align: center;
  text-align: center;
}
@media (max-width: 960px) {
  .count__number {
    font-size: 32px;
  }
}
@media (max-width: 760px) {
  .count__number {
    font-size: 22px;
  }
}
@media (max-width: 425px) {
  .count__number {
    font-size: 16px;
  }
}
.count__day {
  display: block;
  text-align: center;
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 1.2;
  color: var(--light-color-opacity);
}
@media (max-width: 760px) {
  .count__day {
    font-size: 14px;
  }
}
@media (max-width: 425px) {
  .count__day {
    font-size: 12px;
  }
}

.bottom-block {
  background-color: var(--second-bg);
  margin-top: 218px;
  padding: 36px 0;
}
@media (max-width: 960px) {
  .bottom-block {
    margin-top: 150px;
  }
}
@media (max-width: 760px) {
  .bottom-block {
    margin-top: 100px;
  }
}
@media (max-width: 425px) {
  .bottom-block {
    margin-top: 60px;
  }
}
.bottom-block .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 960px) {
  .bottom-block .container {
    flex-wrap: wrap;
  }
}
.bottom-block__people {
  padding-left: 81px;
  padding-right: 81px;
}
@media (max-width: 960px) {
  .bottom-block__people {
    padding-left: 30px;
    padding-right: 0;
  }
}
@media (max-width: 760px) {
  .bottom-block__people {
    padding-left: 0;
    margin-top: 30px;
    flex-basis: 100%;
  }
}
.bottom-block__member {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}
.bottom-block__member:last-child {
  margin-bottom: 0;
}
.bottom-block__title {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: var(--light-color-opacity);
}
.bottom-block__number {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  color: var(--light-color);
  padding-left: 30px;
}
.bottom-block__earned {
  flex-grow: 1;
}
@media (max-width: 960px) {
  .bottom-block__earned {
    margin-top: 30px;
    flex-basis: 100%;
  }
}
.bottom-block__earned .bottom-block__member {
  justify-content: flex-start;
}

.btn--secondary {
  background: linear-gradient(94.78deg, #df5950 11.19%, #451046 93.72%);
  font-weight: 700;
}

.progress__bg {
  max-width: 714px;
  height: 6px;
  background: var(--light-color);
}
@media (max-width: 960px) {
  .progress__bg {
    max-width: 100%;
  }
}
.progress__bar {
  height: 6px;
  background: linear-gradient(94.78deg, #df5950 11.19%, #451046 93.72%);
}
.progress__score {
  display: flex;
  justify-content: space-between;
}
.progress__number {
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 1.2;
  color: var(--light-color-opacity);
}